import React from "react";
import "./styles.css";
import "@wokwi/elements";

const arduinoCode = `void setup() {
  // initialize digital pin LED_BUILTIN as an output.
  pinMode(LED_BUILTIN, OUTPUT);
}

// the loop function runs over and over again forever
void loop() {
  digitalWrite(LED_BUILTIN, HIGH);  // turn the LED on (HIGH is the voltage level)
  delay(1000);                      // wait for a second
  digitalWrite(LED_BUILTIN, LOW);   // turn the LED off by making the voltage LOW
  delay(1000);                      // wait for a second
}`;

export default function App() {
  const runCode = async () => {
    // Compile Arduino source code
    const result = await fetch("https://hexi.wokwi.com/build", {
      method: "post",
      body: JSON.stringify({ arduinoCode }),
      headers: {
        "Content-Type": "application/json"
      }
    });
    const { hexi, stderr } = await result.json();
    if (!hexi) {
      alert(stderr);
      return;
    }
    console.log(hexi);

    //   Set up simuilation
    // Attach virtual hardware
    // Run simulation
  };

  return (
    <div className="App">
      <h1>Arduino Simulator</h1>
      <h2>Start editing to see some magic happen!</h2>
      <wokwi-led color="red" label="" lightcolor=""></wokwi-led>
      <button onClick={runCode}>Run</button>
      <textarea
        value={arduinoCode}
        readOnly
        style={{ width: "100%" }}
        rows="20"
      />
    </div>
  );
}
